import React from 'react';
import { nanoid } from 'nanoid';
import { useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { Button, Grid, Box } from '@mui/material';
import { useLoanAgreementList } from '@/core/hooks';
import { Table, Loader } from '@/components';
import TableToolbar from '@/components/table/tableToolbar';
import FilterListIcon from '@mui/icons-material/FilterList';
import CreateNewFolderIcon from '@mui/icons-material/CreateNewFolder';

const headCells = [
    {
        id: 'contractNumber',
        label: '№'
    },
    {
        id: 'contractDate',
        type: 'date',
        label: 'Дата'
    },
    {
        id: 'client',
        label: 'Клиент'
    },
    {
        id: 'contractAmount',
        label: 'Сумма'
    },
    {
        id: 'currencyId',
        type: 'currency',
        label: 'Валюта'
    },
    {
        id: 'periodType',
        label: 'Процент'
    },
    {
        id: 'maturityDate',
        type: 'date',
        label: 'Дата возврата'
    },
    {
        id: 'agentContractId',
        label: 'Агент'
    },
    {
        id: 'status',
        label: 'Статус'
    }
];

function LoanAgreementList() {
    const navigate = useNavigate();
    const [selected, list, handleSelectAllTableRows, handleSelectTableRow] = useLoanAgreementList([]);
    const { pending } = useSelector((state) => ({ pending: state.lists.pending }));
    const handleNavigate = () => navigate('/contracts/loan-agreement/create');

    if (pending) {
        return <Loader />;
    }
    return (
        <>
            <Box gridColumn="span 12" sx={{ display: 'flex', justifyContent: 'right' }}>
                <Button
                    variant="contained"
                    startIcon={<CreateNewFolderIcon />}
                    sx={{ background: '#0064ff', mr: 3 }}
                    onClick={handleNavigate}
                >
                    <span style={{ fontWeight: 'bold' }}>Создать</span>
                </Button>
            </Box>
            <Box gridColumn="span 12">
                <Table
                    headCells={headCells}
                    rows={list}
                    selected={selected}
                    handleSelectAllTableRows={handleSelectAllTableRows}
                    handleSelectTableRow={handleSelectTableRow}
                >
                    <TableToolbar numSelected={selected.length}>
                        <Button variant="contained" startIcon={<FilterListIcon />}>
                            Фильтр
                        </Button>
                    </TableToolbar>
                </Table>
            </Box>
        </>
    );
}

export default LoanAgreementList;
